@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  max-width: 100vw;
  font-family: 'Orbitron', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 3px;
  font-size: 22;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn {
    cursor: pointer;
}

.bg-image {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    z-index: -100;
}

@media screen and (max-width: 700px) {
    body {
        padding: 0;
    }
    .container {
        margin: 0;
        padding: 0;
    }
}

.container {
    width: 100%;
    margin: 0;
    padding-top: 160px;
    padding-bottom: 40px;
    background-repeat: no-repeat;
    background-size: cover;
}

@media screen and (max-width: 500px) {
    .container {
        padding-top: 90px;
    }
}


.player-panel {
    position: fixed;
    top: 0;
    width: 100%;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 2px solid rgba(0, 0, 0);
    font-size: 18px;
    background-image: url(/step-sequencer/static/media/brushed-aluminium.312149e7.jpg);
}
.player-controls {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.stopped {
    background-color: rgb(129, 172, 43);
    padding-left: 5px;
}
.playing {
    background-color: yellowgreen;
}

.play-stop-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90px;
    width: 90px;
    border-radius: 15px;
    box-shadow: 0 0 5px black;
}

.play-stop-btn img {
    height: 95%;
}

.reset-btn {
    text-transform: uppercase;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 10px;
    border-radius: 10px;
    color: yellowgreen;
}

.reset-btn p {
    margin: 0;
    padding: 0;
}

.reset-btn:active {
    transform: scale(.95);
}

@media screen and (max-width: 450px) {
    .player-panel {
        font-size: 12px;
        padding-bottom: 10px;
    }
    .play-stop-btn {
        height: 40px;
        width: 40px;
        border-radius: 5px;
    }
    .stopped {
        padding-left: 0px;
    }

    .reset-btn {
        padding: 5px;
        border-radius: 4px;
    }
}
#kits {
    background-color: black;
    color: yellowgreen;
    font-size: inherit;
    font-family: inherit;
    letter-spacing: inherit;
    text-transform: uppercase;
    border: none;
}

.presets-menu {
    background-color: black;
    padding: 5px;
    border-radius: 5px;
}


.tempo {
    display: inline-block;
    font-size: inherit;
    background-color: black;
    color: yellowgreen;
    border-radius: 5px;
    text-decoration: none;
    padding: 4px 0;
    min-width: 30px;
    text-align: center;
}

.current-tempo {
    margin: 0;
    background-color: black;
    color: yellowgreen;
    font-size: inherit;
    min-width: 60px;
}

button {
    background: transparent;
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
}

button:hover {
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
}

button:active {
    outline: none;
    border: none;
}

button:focus {
    outline: 0;
}

@media screen and (max-width: 450px) {
    .tempo {
      min-width: 30px;
      padding: 3px;
    }
    .current-tempo {
      min-width: 40px
    }
}


.indicator-panel {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}

.indicators {
    padding: 0 5px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin-top: 10px;
}

.beat-indicator:nth-child(1) {
    height: 16px;
    width: 16px;
}
.beat-indicator:nth-child(4n+5) {
    height: 16px;
    width: 16px;
}

.beat-indicator {
    height: 12px;
    width: 12px;
    background-color: black;
    border-radius: 50%;
    display: inline-block;
}

.active {
    background-color: red;
}

@media screen and (max-width: 450px) {
    .indicators {
        margin-top: 20px 0;
        padding: 0 3px;
    }
    .beat-indicator:nth-child(1) {
        height: 14px;
        width: 14px;
    }
    .beat-indicator:nth-child(4n+5) {
        height: 14px;
        width: 14px;
    }
    .beat-indicator {
        height: 10px;
        width: 10px;
        background-color: black;
        border-radius: 50%;
        display: inline-block;
    }
}

.samples-section {
    font-size: 20px;
    text-align: center;
    padding: 0 10px;
}

@media screen and (max-width: 450px) {
    .samples-section {
        padding: 0;
    }
}

.sample-row-pads {
    width: 100%;
    font-size: inherit;
    display: flex;
    justify-content: space-evenly;
    padding: 5px;
}

.quarter {
    display: flex;
    justify-content: space-between;
}

.sample-row {
    background-color: black;
    border-radius: 10px;
    margin-top: 20px;
}

.sample-row h4 {
    margin: 0;
    color: aliceblue;
    text-align: right;
}

@media screen and (max-width: 450px) {

    .sample-inputs-container h4 {
        width: 100%;
        text-align: center;
    }

    .sample-inputs-content {
        width: 100%;
    }

    .sample-row-pads {
        padding: 5px 2px;
    }
}

:root {
    --first-quarter:  #323643;
    --second-quarter: #606470;
    --third-quarter:  #233142;
    --fourth-quarter: #407088; 
    --active: #f0d43a;  
    --playing: #c50d66;
}

.beat-pad {
    display: inline-block;
    height: 50px;
    width: 5.5%;
    border-radius: 10%;
    background-color: #407088;
    background-color: var(--fourth-quarter);
}

.beat-pad.btn.active {
    background-color: #f0d43a;
    background-color: var(--active);
}

.beat-pad.btn.active.playing {
    background-color: #c50d66;
    background-color: var(--playing);
}

/* beat 1 */
.beat-pad:nth-child(-n+4) {
    background-color: #323643;
    background-color: var(--first-quarter);
}

/* beat 2 */
.beat-pad:nth-child(5) {
    background-color: #606470;
    background-color: var(--second-quarter);
}
.beat-pad:nth-child(6) {
    background-color: #606470;
    background-color: var(--second-quarter);
}
.beat-pad:nth-child(7) {
    background-color: #606470;
    background-color: var(--second-quarter);
}
.beat-pad:nth-child(8) {
    background-color: #606470;
    background-color: var(--second-quarter);
}

/* beat 3 */
.beat-pad:nth-child(9) {
    background-color: #233142;
    background-color: var(--third-quarter);
}
.beat-pad:nth-child(10) {
    background-color: #233142;
    background-color: var(--third-quarter);
}
.beat-pad:nth-child(11) {
    background-color: #233142;
    background-color: var(--third-quarter);
}
.beat-pad:nth-child(12) {
    background-color: #233142;
    background-color: var(--third-quarter);
}


/* beat 4 */
.beat-pad:nth-last-child(-n+4) {
    background-color: #407088;
    background-color: var(--fourth-quarter);
}

@media screen and (max-width: 450px) {
    .beat-pad {
        height: 30px;
    }
}


.rs-range {
    width: 80%;
    -webkit-appearance: none;
}
.rs-range:focus {
    outline: none;
}
.rs-range::-webkit-slider-runnable-track {
    width: 100%;
    height: 2px;
    cursor: pointer;
    box-shadow: none;
    background: #ffffff;
    border-radius: 0px;
    border: 0px solid #010101;
}
.rs-range::-moz-range-track {
    width: 100%;
    height: 2px;
    cursor: pointer;
    box-shadow: none;
    background: #ffffff;
    border-radius: 0px;
    border: 0px solid #010101;
}
  
.rs-range::-webkit-slider-thumb {
    box-shadow: none;
    border: 0px solid #ffffff;
    box-shadow: 0px 10px 10px rgba(0,0,0,0.25);
    height: 16px;
    width: 5px;
    border-radius: 22px;
    background: rgba(255,255,255,1);
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -8px;
}
.rs-range::-moz-range-thumb{
    box-shadow: none;
    border: 0px solid #ffffff;
    box-shadow: 0px 10px 10px rgba(0,0,0,0.25);
    height: 16px;
    width: 5px;
    border-radius: 22px;
    background: rgba(255,255,255,1);
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -8px;
}
.rs-range::-moz-focus-outer {
    border: 0;
}


.sample-inputs-container {
    padding-bottom: 4px;
    display: block;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
}
.sample-inputs-container h4{
    padding-left: 10px;
    display: inline-block;
}

.sample-inputs-content {
    display: flex;
    justify-content: space-evenly;
}
.sample-input {
    display: inline-block;
}

.sample-input label {
    margin-top: 5px;
    padding: 0;
    color: whitesmoke;
    font-size: 12px;
    display: block;
    min-width: 120px;
    text-align: center;
}



